<Page name="home">
  <!-- Top Navbar -->
  <Navbar large sliding={false}>
    <NavLeft>
      <Link iconIos="f7:clock" iconAurora="f7:clock" iconMd="f7:clock" on:click={()=>{ historyPopup.open() }} />
    </NavLeft>
    <NavTitle sliding>Check In/Out</NavTitle>
    <NavRight>
      <Link iconMaterial="refresh" on:click={reset}/>
    </NavRight>
    <NavTitleLarge>
      Check In/Out <span style="font-size: 11px; opacity: .33">v{version}</span>
    </NavTitleLarge>
  </Navbar>

  
  {#if checkInOutData.length > 0}
    <div style="text-align:right; padding-right: 10px;">
      <span style=" font-size:28px;">
        <span style="font-size:12px; width:100%; text-align:right">Working Timer</span>
        <div style="height:1px"></div>
        <i class="f7-icons" style="margin-right: 0px; font-size: 28px; color: #ccc">clock_fill</i>
        <span style="">
          {timer}<span style="font-size: 16px; width:25px; display: inline-block;">{second}</span>
        </span>
      </span>      
    </div>
    <!-- <div style="height:1px"></div> -->

    <!-- <BlockTitle>Check In/Out Record</BlockTitle> -->
    <List inset>
    {#each checkInOutData as item}
      <!-- <ListItem
        title={item.type + ' - ' + item.time}
      /> -->
      <li>
        <div class="item-link item-content no-chevron" on:click={()=>{edit(item)}}>
          <div class="item-media">
            <Icon color={item.type === 'check-in' ? 'green' : 'orange'} 
              f7={item.type === 'check-in' ? "arrow_right_square_fill" : "arrow_left_square_fill"} />
          </div>
          <div class="item-inner">
            <div class="item-title">
              <div class="item-header">{item.type}</div>
              {item.time}
            </div>
            <div class="item-after">
              <!-- <Icon f7="square_pencil" /> -->
            </div>
          </div>
        </div>
      </li>
    {/each}
    </List>
  {/if}

  <BlockTitle>{message}</BlockTitle>

  <div style="height: 100px; width: 100%;"></div>


  <div class="bottom-button-container">
    {#if checkInOutData.length >= 4}
      <Button large fill raised color="deeporange" on:click={reset} >Reset</Button>
    {:else}
      <Button large fill raised on:click={onCheckInOutButtonClick} >Check in/out <div style="position: absolute; right: 15px; top: 0px; opacity: 0.55; font-size: 0.9em;">({currentTime})</div></Button>
    {/if}
  </div>

  <Actions bind:this={actionSheet}>
    <ActionsGroup>
      <!-- <ActionsLabel>Edit</ActionsLabel> -->
      <ActionsButton>Edit</ActionsButton>
      <ActionsButton color="red">Delete</ActionsButton>
    </ActionsGroup>
    <ActionsGroup>
      <ActionsButton color="red">Cancel</ActionsButton>
    </ActionsGroup>
  </Actions>

  <Popup swipeToClose bind:this={historyPopup}>
    <Page>
      <Navbar>
        <NavLeft>
          <a href="#" on:click={()=>{ historyPopup.close() }} class="link">
              <i class="icon icon-back"></i>
              <span>Back</span>
          </a>
        </NavLeft>
        <NavTitle sliding>History</NavTitle>
        <NavRight>
          <Link iconF7="trash" on:click={deleteHistory}/>
        </NavRight>        
      </Navbar>  

      {#if historyDisplayData.length > 0}
        {#each [...historyDisplayData].reverse() as item}
          <BlockHeader>Week {item.week} <span style="float:right">Balance: {numberToTimeString(36 - item.totalWorkingHourDecimal).replace('0-','-')}</span></BlockHeader>
          <List>
          {#each [...item.items].reverse() as item2}
            <ListItem link="#" noChevron on:click={()=>{showHistoryDetail(item2)}} footer={item2.date} title={`${moment(item2.dateObj).format('dddd')}`} after={item2.workingHour}></ListItem>
          {/each}
            <ListItem after={`Total: ${numberToTimeString(item.totalWorkingHourDecimal).replace('0-','-')}`}></ListItem>
          </List>
        {/each}
      {/if}
    </Page>
  </Popup>

  <Popup swipeToClose bind:this={historyDetailPopup}>
    <Page>
      {#if historyDetailData != null}
        <Navbar>
          <NavLeft>
            <a href="#" on:click={()=>{ historyDetailPopup.close() }} class="link">
                <i class="icon icon-back"></i>
                <span>Back</span>
            </a>
          </NavLeft>
          <NavTitle sliding>{`${moment(historyDetailData.dateObj).format('ddd')}`}, {historyDetailData.date}</NavTitle>
          <NavRight>
          </NavRight>        
        </Navbar>  

        {#if historyDetailData.checkInOutData && historyDetailData.checkInOutData.length > 0}
          <List inset>
          {#each historyDetailData.checkInOutData as item}
            <li>
              <div class="item-content">
                <div class="item-media">
                  <Icon color={item.type === 'check-in' ? 'green' : 'orange'} 
                    f7={item.type === 'check-in' ? "arrow_right_square_fill" : "arrow_left_square_fill"} />
                </div>
                <div class="item-inner">
                  <div class="item-title">
                    <div class="item-header">{item.type}</div>
                    {item.time}
                  </div>
                </div>
              </div>
            </li>
          {/each}
          </List>
        {/if}
      {/if}
    </Page>
  </Popup>
  
  
</Page>
<script>
  import {
    f7,
    Page,
    Navbar,
    NavLeft,
    NavTitle,
    NavTitleLarge,
    NavRight,
    Link,
    Toolbar,
    Block,
    BlockTitle,
    List,
    ListItem,
    Row,
    Col,
    Icon,
    Button,
    Actions, ActionsGroup, ActionsLabel, ActionsButton,
    Popup,
    BlockHeader,
    BlockFooter,

  } from 'framework7-svelte';

  import moment from 'moment';
  import { onMount } from 'svelte';
  import config from '../js/config.json';

  let version = config.version;
  let checkInOutData = [
    // {type:'check-in', time:'09:00'},
    // {type:'check-out', time:'12:00'},
  ];
  let message = '';
  let actionSheet;
  let historyPopup;
  let history = JSON.parse(window.localStorage.getItem('history')) || [];
  let historyDisplayData = [];
  let curDate = '';

  let historyDetailPopup;
  let historyDetailData;


  function reset(){
    f7.dialog.confirm('','Reset?',()=>{
      checkInOutData = [];
      message = '';

      window.localStorage.setItem('checkInOutData', JSON.stringify(checkInOutData));
    });
    
  }

  function onCheckInOutButtonClick(){
    f7.dialog.confirm('', checkInOutData.length % 2 == 0 ? 'Check In?' : 'Check Out?', ()=>{  
      checkInOutData.push({
        type: checkInOutData.length % 2 == 0 ? 'check-in' : 'check-out',
        datetime: new Date(),
        time: moment(new Date()).format('HH:mm'),
      });

      curDate = moment(new Date()).format('YYYY-MM-DD');

      updateMessage();

      checkInOutData = checkInOutData;

      window.localStorage.setItem('checkInOutData', JSON.stringify(checkInOutData));
    });    
  }

  function edit(item){
    let itemDate = moment(item.datetime).format('YYYY-MM-DD');
    curDate = moment(new Date()).format('YYYY-MM-DD');
    if(itemDate !== curDate)
      return;
    f7.dialog.prompt('Time', null, (value)=>{
      item.time = value;
      checkInOutData = checkInOutData;
      // curDate = moment(new Date()).format('YYYY-MM-DD');
      updateMessage();
      window.localStorage.setItem('checkInOutData', JSON.stringify(checkInOutData));
    }, null, item.time);
  }

  function updateMessage(){
    if(checkInOutData.length == 1 || checkInOutData.length == 2){
      let timeToLeave = numberToTimeString(timeStringToNumber(checkInOutData[0].time) + 1 + 7.25);
      message = `You can leave at ${timeToLeave} at the earliest`;
    }
    else if(checkInOutData.length == 3){
      let part1 = timeStringToNumber(checkInOutData[1].time) - timeStringToNumber(checkInOutData[0].time);
      
      let check3 = timeStringToNumber(checkInOutData[2].time);

      let breakTime = check3 - timeStringToNumber(checkInOutData[1].time);
      if(breakTime < 1){
        check3 = timeStringToNumber(checkInOutData[1].time) + 1;
      }
      let timeToLeave = numberToTimeString(7.25 - part1 + check3);
      message = `You can leave at ${timeToLeave}`;
    }
    else if(checkInOutData.length == 4){
      let part1 = timeStringToNumber(checkInOutData[1].time) - timeStringToNumber(checkInOutData[0].time);
      
      let check3 = timeStringToNumber(checkInOutData[2].time);

      let breakTime = check3 - timeStringToNumber(checkInOutData[1].time);
      if(breakTime < 1){
        check3 = timeStringToNumber(checkInOutData[1].time) + 1;
      }

      let part2 = timeStringToNumber(checkInOutData[3].time) - check3;

      let workingHour = numberToTimeString(part1 + part2);
      let workingHourDecimal = part1 + part2;

      message = `You have worked ${workingHour} hours today`;

      upateHistory(workingHour, workingHourDecimal);
    }

    // alert(moment((new Date()).setDate(new Date().getDate()+7)).week());
  }

  function upateHistory(workingHour, workingHourDecimal){
    let date = moment(new Date()).format('YYYY-MM-DD');
    if(curDate === date){
      let index = history.findIndex(x => x.date === date);
      if(index >= 0)
        history.splice(index, 1);
      history.push({
        date: date, 
        week: moment(new Date()).format('W'), 
        workingHour: workingHour, 
        workingHourDecimal: workingHourDecimal, 
        dateObj: new Date(),
        checkInOutData: checkInOutData,
        });
      window.localStorage.setItem('history', JSON.stringify(history));
      history = history;
      makeHistoryDisplayData();
    }
  }

  function deleteHistory(){
    f7.dialog.confirm('','Delete history?',()=>{
      window.localStorage.removeItem('history');
      history = [];
      makeHistoryDisplayData();
    });  
  }

  function makeHistoryDisplayData(){
    historyDisplayData = [];
    let curWeek = -1;
    let curObj = {};
    history.forEach(item => {
      if(curWeek != item.week){
        curWeek = item.week;
        curObj = {};
        curObj.week = item.week;
        curObj.items = [];        
        curObj.totalWorkingHourDecimal = 0;
        historyDisplayData.push(curObj);
      }
      curObj.items.push(item);
      curObj.totalWorkingHourDecimal += item.workingHourDecimal;
    });
    historyDisplayData = historyDisplayData;
  }

  function showHistoryDetail(item){
    historyDetailData = item;
    historyDetailPopup.open();
  }


  function timeStringToNumber(time) {
    return ((new Date(("01 Jan 1970 " + time || "").replace(".", ":") + " GMT")) / 3600000) || 0;
  }

  function numberToTimeString(decimalTimeString){
    let isNegative = false;
    if(decimalTimeString < 0){
      isNegative = true;
      decimalTimeString = decimalTimeString * -1;
    }
    var decimalTime = parseFloat(decimalTimeString);
    decimalTime = decimalTime * 60 * 60;
    var hours = Math.floor((decimalTime / (60 * 60)));
    decimalTime = decimalTime - (hours * 60 * 60);
    var minutes = Math.floor((decimalTime / 60));
    decimalTime = decimalTime - (minutes * 60);
    var seconds = Math.round(decimalTime);
    if(hours < 10)
    {
      hours = "0" + hours;
    }
    if(minutes < 10)
    {
      minutes = "0" + minutes;
    }
    if(seconds < 10)
    {
      seconds = "0" + seconds;
    }
    return `${isNegative ? '-' : ''}${hours}:${minutes}`;
  }

  let timer = '--:--';
  let second = '--';
  let currentTime = moment(new Date()).format('HH:mm:ss');

  setInterval(() => {
    currentTime = moment(new Date()).format('HH:mm:ss');
  }, 100);

  setInterval(() => {
    if(checkInOutData.length == 1){
      
      timer = numberToTimeString(timeStringToNumber(moment(new Date()).format('HH:mm')) - timeStringToNumber(checkInOutData[0].time)); 
      second = ''+moment(new Date()).format('ss');
    }
    else if(checkInOutData.length == 2){
      timer = numberToTimeString(timeStringToNumber(checkInOutData[1].time) - timeStringToNumber(checkInOutData[0].time));
      second = '00';
    }
    else if(checkInOutData.length == 3){
      let part1 = timeStringToNumber(checkInOutData[1].time) - timeStringToNumber(checkInOutData[0].time);
      
      let check3 = timeStringToNumber(checkInOutData[2].time);

      let breakTime = check3 - timeStringToNumber(checkInOutData[1].time);
      if(breakTime < 1){
        check3 = timeStringToNumber(checkInOutData[1].time) + 1;
      }
      
      let now = timeStringToNumber(moment(new Date()).format('HH:mm'));
      if(now >= check3){
        timer = numberToTimeString((part1 + now) - check3);
        second = ''+moment(new Date()).format('ss');
      }
      else{
        timer = numberToTimeString(timeStringToNumber(checkInOutData[1].time) - timeStringToNumber(checkInOutData[0].time));
        second = '00';
      }
    }
    else if(checkInOutData.length == 4){
      let part1 = timeStringToNumber(checkInOutData[1].time) - timeStringToNumber(checkInOutData[0].time);
      
      let check3 = timeStringToNumber(checkInOutData[2].time);

      let breakTime = check3 - timeStringToNumber(checkInOutData[1].time);
      if(breakTime < 1){
        check3 = timeStringToNumber(checkInOutData[1].time) + 1;
      }

      let part2 = timeStringToNumber(checkInOutData[3].time) - check3;

      if(part2 < 0)
        part2 = 0;

      let workingHour = numberToTimeString(part1 + part2);
      let workingHourDecimal = part1 + part2;

      timer = workingHour;
      second = '00';
    }
  }, 100);

  onMount(() => {
    let checkInOutDataSave = window.localStorage.getItem('checkInOutData');
    if(checkInOutDataSave){
      checkInOutData = JSON.parse(checkInOutDataSave);
      updateMessage();
    }
    makeHistoryDisplayData();
  });


</script>

<style>
  .bottom-button-container{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    color: white;
    text-align: center;
    padding: 20px;
    padding-bottom: 40px;
    z-index: 10;
    box-sizing: border-box;
  }
</style>